.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 25vh;
}
.footer-image {
  position: absolute;
  bottom: 25px;
  left: 0;
  transform: translateY(25vh);
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition-duration: 0.7s;
}
.footer-image.active {
  transform: translateY(0);
}
.footer-text {
  position: absolute;
  background: rgb(242,151,27);
  width: 100%;
  height: 50px;
  bottom: 0;
}